* {
  box-sizing: border-box;
}

body, ol, ul, li, h2, p, div, a {
  margin: 0;
  padding: 0;
}

ul, ol {
  list-style-type: none;
}

img {
  display: block;
}

.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: block;
}

.nav__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: inherit;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition-property: color;
  transition-duration: .3s;
  display: block;
}

.nav__link:is(:hover, :focus) {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  place-content: center;
  justify-items: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

.card {
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
  transition-property: transform;
  transition-duration: .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  transition-property: color;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rank {
  justify-content: space-between;
  align-items: stretch;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  margin-bottom: 16px;
  font-size: 12px;
  display: flex;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  width: 100%;
  transition-property: background-color, outline, outline-offset, color, cursor;
  transition-duration: .3s;
}

.card__buy-button:hover {
  outline-offset: -1px;
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  outline: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.button {
  text-align: center;
  border: none;
  border-radius: 5px;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}
